.App{
    width: 100%;
    height: 100vh;
    padding-top: 10px;
    background-color: #1b1b32;
    font-family: Lato, sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.titulo-cont{
    width: 100%;
    height: 100px;
    margin: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo{
    height: 50px;
}

h1{
    color: #fff;
    margin-left: 10px;
    font-size: 60px;
}

.contenedor-calcu{
    width: 400px;
    height: 600px;
    padding: 14px;
    background-color: #99c9ff;
    border-radius: 20px;
    border: 5px solid white;
}

.fila{
    margin: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}
